<template>
  <div>
    <el-card
      v-for="item in markedSite"
      :key="item.name"
      shadow="hover"
      class="markedSite-container"
    >
      <a :href="item.url">
        <img
          :src="item.iconUrl"
          class="markedSite-ico"
          onerror=''
        />
        <div
          :title="item.name"
          class="markedSite-name"
        >{{item.name}}</div>
      </a>
    </el-card>
  </div>
</template>
<script>
import { queryMarkedSite } from './../assets/api/index'
export default {
  data () {
    return {
      markedSite: []
    }
  },
  created () { this.getMarkedSite() },
  methods: {
    getMarkedSite () {
      queryMarkedSite().then(data => {
        this.markedSite = data
      })
    }
  }
}
</script>
<style scoped>
.markedSite-container {
  float: left;
  width: 104px;
  height: 120px;
  margin: 10px;
  text-align: center;
}
.markedSite-ico {
  width: 64px;
  height: 64px;
}
.markedSite-name {
  width: 64px;
  word-break: keep-all;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
</style>



